// SIZES
:root{
    
    --s-p: 1rem;
}

// FONTS
:root{

    --f-header: Roboto;
    --f-text: Roboto Slab;
}

// FIXED SIZES
$s-menuItemWidth: 8rem;
$s-menuItemPadding: 1rem;
$s-m-menuMaxHeight: 1000px;

// TRANSITIONS
$t-smooth: ease .5s;